import { AnyAction } from "redux";
import {
  WS_CONNECTION_CLOSED,
  WS_CONNECTION_ERROR,
  WS_CONNECTION_SUCCESS,
  WS_GET_MESSAGE,
} from "../actions/wsAction";

type TWSState = {
  wsConnected: boolean;
  messages: any[];
  total: number;
  totalToday: number;
  error: any;
};

const initialState: TWSState = {
  wsConnected: false,
  messages: [],
  total: 0,
  totalToday: 0,
  error: null,
};

//    WebSocket
export const wsReducer = (
  state = initialState,
  action: AnyAction
): TWSState => {
  switch (action.type) {
    //      WS_CONNECTION_SUCCESS
    //   wsConnected   true
    case WS_CONNECTION_SUCCESS:
      return {
        ...state,
        error: undefined,
        wsConnected: true,
      };

    //      WS_CONNECTION_ERROR
    //   wsConnected   false     action.payload
    case WS_CONNECTION_ERROR:
      return {
        ...state,
        error: action.payload,
        wsConnected: false,
      };

    //      WS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case WS_CONNECTION_CLOSED:
      return {
        ...state,
        error: undefined,
        wsConnected: false,
      };

    //      WS_GET_MESSAGE
    //  ,     
    //  messages  ,    
    case WS_GET_MESSAGE:
      const { orders } = action.payload;
      return {
        ...state,
        error: undefined,
        messages: orders,
        total: action.payload.total,
        totalToday: action.payload.totalToday,
      };
    default:
      return state;
  }
};
